*, h1, h2, p {
  margin: 0;
  padding: 0;
}

body {
  background-color: #f7eded;
  margin: 10px 50px 30px;
  font-family: Arial, Helvetica, sans-serif;
  overflow-x: hidden;
}

.div {
  cursor: auto;
}

.select-container {
  margin-top: 20px;
}

.breed-select, #single {
  width: 100px;
  margin-bottom: 10px;
  padding: 8px;
  font-size: 16px;
}

.box {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.cat-info {
  gap: 30px;
  display: flex;
}

.text-container {
  flex-direction: grid;
  justify-content: space-between;
  gap: 16px;
  display: flex;
}

.title, .text, .subtitle, .loader {
  color: #22343d;
  font-family: sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: normal;
}

.text {
  font-size: 16px;
}

.subtitle {
  font-size: 22px;
  font-weight: 600;
}

.error {
  font-weight: 16px;
  color: red;
  font-weight: 500;
}

.is-hidden {
  display: none;
}

.img-style {
  border-radius: 8px;
}

.container {
  max-width: 1024px;
  margin: 0 auto;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 4px solid #fff;
  border-right: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  animation: rotation 1s linear infinite;
  display: flex;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-bottom: 4px solid rgba(0, 0, 0, 0);
  border-left: 4px solid #ff3d00;
  border-radius: 50%;
  animation: rotation .5s linear infinite reverse;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

#search-form {
  margin-top: 20px;
}

#search-input {
  width: 200px;
  padding: 8px;
  font-size: 16px;
}

#search-button {
  cursor: pointer;
  padding: 8px 16px;
  font-size: 16px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  margin-top: 20px;
  display: grid;
}

